import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class NeckerFoot extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'necker-foot',
			'clearfix',
			className
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

NeckerFoot.propTypes = {
	className: PropTypes.string,
	children: PropTypes.oneOfType([
		PropTypes.array,
		PropTypes.object,
		PropTypes.string
	])
};

export default NeckerFoot;